.game-result {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0, 2);

    display: flex;
    align-items: center;
}

.game-result-inner {
    width: 800px;
    height: 384px;
    border-radius: 10px;
    padding-left: 70px;

    display: flex;
    align-items: center;
}

.game-result img {
    max-height: 75%;
}
